<template lang="pug">
  .list-lightbox(v-if="lightboxList.length > 0" @click="callLightbox")
    img(:src="lightboxList[0]")

    template(v-if="isActive" v-for="(node, index) in lightboxList")
      .lightbox(:key="index" v-if="node !== ''" :class="{ active: isActive && index === currentIndex }" @click.stop="closeLightbox")
        template(v-if="lightboxList.length > 1")
          .prev(@click.stop="handleClickArrow(-1)")
          .next(@click.stop="handleClickArrow(1)")
        .close
        .image-box
          img.lightbox-img(:src="node" @click.stop="openLightbox")
</template>

<script>
export default {
	name: "list-lightbox",
	
	props: {
		srcs: Array
	},

  data() {
    return {
      isActive: false,
      currentIndex: 0,
    };
  },

  computed: {
    lightboxList () {
      // 過濾原始資料
      return this.srcs.filter(lightbox => !['', null, undefined].includes(lightbox))
    },
  },

  created () {
  },

  methods: {
    handleClickArrow (data) {
      this.currentIndex = (this.currentIndex + data + this.lightboxList.length) % this.lightboxList.length
    },
  
    callLightbox () {
      this.isActive = true
      this.currentIndex = 0
    },
    closeLightbox () {
      this.isActive = false
    },
    openLightbox () {
      this.isActive = true
    },
  }
};
</script>

<style lang="scss" scoped>
@import "./style";
</style>
