import Vue from "vue";
import Router from "vue-router";
import Welcome from "./views/welcome/index.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "welcome",
      component: Welcome
    },{
      path: "/home",
      name: "home",
      component: () => import("./views/welcome/home.vue")
    },
    {
      path: "/about",
      name: "about",
      component: () => import("./views/welcome/about.vue")
    },
    {
      path: "/skill-tree",
      name: "skill-tree",
      component: () => import("./views/welcome/skill-tree.vue")
    },
    {
      path: "/works",
      name: "works",
      component: () => import("./views/welcome/works.vue")
    }
  ]
});
